@import url(https://fonts.googleapis.com/css?family=Cutive|Cutive+Mono);
body {
  background: -webkit-linear-gradient(left bottom, #0e04d1, #c90097, #fb0059, #ff5d27, #eba612);
  background: linear-gradient(to right top, #0e04d1, #c90097, #fb0059, #ff5d27, #eba612);
  font-family: "Cutive", serif;
  min-height: 100%;
  width: 100%;
  -webkit-transition: all 1s;
  transition: all 1s; }

#app {
  width: 100%; }

::-webkit-scrollbar {
  width: 0px;
  /* remove scrollbar space */
  background: transparent;
  /* optional: just make scrollbar invisible */ }

.wrapper-container {
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100%; }
  .wrapper-container .dark {
    background: #212121;
    color: #f2f2f2; }
  .wrapper-container .light {
    background: #e6e6e6;
    color: black; }
  .wrapper-container .flexbox-wrap {
    display: flex;
    flex-direction: column;
    margin: 0 0.12%;
    width: 49.5%;
    height: 100%; }
    .wrapper-container .flexbox-wrap .textarea {
      padding: 80px 15px;
      padding-bottom: 10px;
      overflow: scroll;
      overflow-x: hidden;
      font-size: calc(0.6em + 0.4vw); }

.editor {
  height: 90vh;
  margin-right: 0.1px; }
  .editor textarea {
    padding: 80px 15px;
    padding-bottom: 5px;
    box-sizing: border-box;
    background: #212121;
    color: #f2f2f2;
    font-size: calc(0.9em + 0.2vw);
    outline: none;
    border: none;
    width: 100%;
    height: 85%; }

.preview {
  word-wrap: break-word;
  line-height: 1.4;
  margin-left: 0; }
  .preview blockquote {
    border-left: 3px solid #e60099;
    background: white;
    color: #e60099;
    padding: 0.2px 15px;
    margin-left: 25px; }
  .preview code {
    background: white;
    word-wrap: break-word !important;
    color: #e60099;
    padding: 1px 4px 2px 4px; }
  .preview pre {
    background: white;
    padding: 5px 0 5px 5px; }
  .preview h1 {
    border-bottom: 2px solid white; }
  .preview h2 {
    border-bottom: 1px solid white; }
  .preview table {
    border-collapse: collapse; }
  .preview td, .preview th {
    border: 2px solid #99004d;
    padding: 10px; }

.toolbar {
  height: auto;
  width: 100%;
  z-index: 99;
  background: #2d2d2d;
  box-shadow: 0px 5px 15px black, 0px 4px inset #444; }
  .toolbar > ul {
    margin: 0;
    padding: 0;
    text-align: center;
    list-style-type: none;
    text-decoration: none; }
    .toolbar > ul li {
      display: inline-block;
      box-shadow: 0.5px 0px 5px inset rgba(0, 0, 0, 0.3); }
      .toolbar > ul li .icon {
        font-size: calc(0.9em + 0.8vw);
        margin: 8px 1.3vw;
        padding: 0  0.5vw;
        cursor: pointer; }
        .toolbar > ul li .icon svg {
          width: 100%;
          height: 100%; }

.header {
  height: calc(2em + 3.5vw); }
  .header h3 {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding: 2px 15px;
    font-size: calc(1em + 1.3vw);
    text-shadow: 0 -1px 0 #fff, 0 1px 0 #2e2e2e, 0 2px 0 #2c2c2c, 0 3px 0 #2a2a2a, 0 4px 0 #282828; }
  .header .arrows {
    display: none;
    cursor: pointer; }

.dark-headers {
  background: #2d2d2d; }

.light-headers {
  background: grey; }

