@media only screen and (max-width: 580px) {
  body .wrapper-container {
    flex-direction: column;
    justify-content: space-around;
    align-items: center; }
    body .wrapper-container .editor {
      height: inherit; }
      body .wrapper-container .editor textarea {
        padding: 20px 15px;
        height: 100%; }
    body .wrapper-container .fullScreen {
      height: 99% !important;
      visibility: visible; }
    body .wrapper-container .minimize {
      height: 0 !important;
      visibility: hidden !important; }
    body .wrapper-container .flexbox-wrap {
      width: 100%;
      height: 49.3%;
      visibility: visible; }
      body .wrapper-container .flexbox-wrap .textarea {
        padding: 20px 15px;
        z-index: 99; }
      body .wrapper-container .flexbox-wrap .header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 20px;
        align-items: center; }
        body .wrapper-container .flexbox-wrap .header .arrows {
          display: inline-flex; } }

